@import '../../global.scss';

.portfolyo{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    h1{
        font-size: 50px;

        @include mobile{
            font-size: 30px;
        }
    }

    ul{
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include mobile{
            flex-wrap: wrap;
            justify-content: center; 
            margin: 10px 0;
        }

        li{
            font-size: 14px;
            margin-left: 50px;
            padding: 7px;
            border-radius: 10px;
            cursor: pointer;

            &.active{
                background-color: $main-color;
                color: white;
            }
        }
    }
    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include mobile{
            width: 100%;
        }

        .item{
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid #bbb;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;

            @include mobile{
                width: 130px;
                height: 100px;
            }

            h3{
                position: absolute;
                font-size: 20px;
            }

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1; 
                border-radius: 20px;
            }

            &:hover{
                background-color: $main-color;
                img{
                    opacity: 0.2;
                    font-size: 20px;
                }
            }
        }
    }
}