@import "../../global.scss";

.menu{
    width: 300px !important;
    height: 100vh !important;
    background-color: $main-color;
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all .5s ease;

    &.active{
        right: 0;
    }
    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 40px;
        font-weight: 300;
        text-decoration: none;
        color: white;
        width: 60%;
        li{
            margin-bottom: 20px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: inherit;
            }
            &:hover{
                font-weight: 500;
            }
        }
    }
}