@import "../../global.scss";

.intro{
    background-color: white;
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }

    .left{
        flex: 0.5;
        overflow: hidden;

        .imgContainer{
            width: 1100px;
            height: 1100px;
            background-color: crimson;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: flex-end;
            float: right;

            @include mobile{
                align-items: flex-start;
            }

            img{
                height: 90%;

                @include mobile{
                    height: 50%;
                }
            }
        }
    }
    .right{
        flex: 0.5;
        position: relative;

        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            @include mobile{
                padding-left: 0;
                align-items: center;
                height: 70%;
            }

            h1{
               font-size: 60px;
               margin:0; 

               @include mobile{
                   font-size: 35px;
               }
            }
            h2{
                font-size: 35px;
                margin:0; 

               @include mobile{
                font-size: 20px;
            }
            }
            h3{
                font-size: 30px;
                margin:0;
                @include mobile{
                    font-size: 20px;
                }

                span{
                    font-size: inherit;
                    color: crimson;
                }
                .ityped-cursor {
                    animation: blink 1s infinite;
                }
                
                @keyframes blink {
                    50% {
                        opacity: 1;
                    }
                    100% {
                        opacity: 0;
                    }
                }
            }
        }
        a{
            position: absolute;
            bottom: 10px;
            left: 40%;

            img{
                width: 30px;
                animation: arrowBlink 2s infinite;
            }
            @keyframes arrowBlink {
                100% {
                    opacity: 0;
                }
            }
        }
    }
}